<template>
  <div class="flex min-h-screen">
    <div class="w-1/5">
      <side-bar></side-bar>
    </div>
    <div class="flex-1 border-x overflow-auto">
      <post-detail v-for="bId in bIdList" :key="bId" :b_id="bId" />
      <infinite-loading @infinite="getBIdList" />
    </div>
    <div class="w-1/5">side menu</div>
  </div>
</template>

<script>
import SideBar from "@/components/SideBar";
import PostDetail from "@/components/post/PostDetail.vue";
import InfiniteLoading from "v3-infinite-loading";
export default {
  name: "PageHome",
  components: {
    SideBar,
    PostDetail,
    InfiniteLoading,
  },
  data() {
    return {
      bIdList: [],
      lastRowNum: 0,
      isLoading: false,
    };
  },
  created() {
    this.getBIdList();
  },
  methods: {
    getBIdList($state) {
      if (this.isLoading) {
        return; // 이미 로딩 중이면 요청을 하지 않음
      }
      this.isLoading = true; // 로딩 시작
      if (!$state) {
        console.error("No $state object available");
        return;
      }
      this.$axios
        .get(`http://192.168.0.84:8083/post/list?lastRowNum=${this.lastRowNum}`)
        .then((res) => {
          const newItems = res.data;
          if (newItems.length) {
            this.bIdList = [...this.bIdList, ...newItems];
            this.lastRowNum += newItems.length;
            console.log(this.bIdList);
            console.log($state);
            $state.loaded(); // 데이터 로드 후 상태 업데이트
          } else {
            $state.complete(); // 더 이상 로드할 데이터가 없을 때
          }
        })
        .catch((err) => {
          console.error(err);
          if ($state) $state.complete();
        })
        .finally(() => {
          this.isLoading = false; // 로딩 완료
        });
    },
  },
};
</script>

